import { UserCircleIcon } from "@heroicons/react/24/solid";

import { motion, MotionProps } from "framer-motion";

export default function HomePage({ role, motion }: { role: string; motion: MotionProps }) {
  return (
    <div className="flex flex-col items-center justify-center min-h-screen bg-[#344CB7] p-6">
      <motion.div 
        initial={{ y: -100, opacity: 0 }}
        animate={{ y: 0, opacity: 1 }}
        transition={{ duration: 1 }}
        className="text-3xl font-bold text-white mb-8"
      >
        Welcome to Star-Software
      </motion.div>
      <motion.div 
        className="w-full max-w-2xl bg-white p-8 rounded-full shadow-lg flex flex-col items-center space-y-6"
      >
        <UserCircleIcon className="w-24 h-24 text-gray-400" />
        {!role && (
          <div className="flex space-x-6">
            <motion.div
              initial={{ x: -100, rotate: -360, opacity: 0 }}
              animate={{ x: 0, rotate: 0, opacity: 1 }}
              whileTap={{ x: -100, rotate: -360, opacity: 0 }}
              transition={{ duration: 1 }}
            >
              <Link
                href="/auth/login"
                className="flex flex-col items-center text-xl font-semibold text-white bg-[#344CB7] p-4 rounded-full w-32 h-32 justify-center hover:bg-blue-700 transition-colors duration-300"
              >
                Admin
              </Link>
            </motion.div>
            <motion.div
              initial={{ x: 100, opacity: 0 }}
              animate={{ x: 0, opacity: 1 }}
              whileTap={{ x: 100, opacity: 0 }}
              transition={{ duration: 1 }}
            >
              <Link
                href="/auth/login"
                className="flex flex-col items-center text-xl font-semibold text-white bg-[#344CB7] p-4 rounded-full w-32 h-32 justify-center hover:bg-blue-700 transition-colors duration-300"
              >
                Manager
              </Link>
            </motion.div>
          </div>
        )}
        {role === "admin" && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            whileTap={{ opacity: 0 }}
            transition={{ duration: 1 }}
          >
            <Link
              href="/admin/dashboard"
              className="block text-xl font-semibold text-white bg-[#344CB7] p-4 rounded-full w-48 text-center hover:bg-blue-700 transition-colors duration-300"
            >
              Go to Admin Dashboard
            </Link>
          </motion.div>
        )}
        {role === "manager" && (
          <motion.div
            initial={{ opacity: 0 }}
            animate={{ opacity: 1 }}
            whileTap={{ opacity: 0 }}
            transition={{ duration: 1 }}
          >
            <Link
              href="/manager/dashboard"
              className="block text-xl font-semibold text-white bg-[#344CB7] p-4 rounded-full w-48 text-center hover:bg-blue-700 transition-colors duration-300"
            >
              Go to Manager Dashboard
            </Link>
          </motion.div>
        )}
      </motion.div>
    </div>
  );
}
